'use client';

import { useAuth } from '@/context/AuthContext';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import Link from 'next/link';

interface DashboardCard {
  title: string;
  description: string;
  href: string;
  roles: string[];
  color: string;
}

const dashboardCards: DashboardCard[] = [
  {
    title: 'Student Management',
    description: 'Add, edit, and manage student records',
    href: '/dashboard/students',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-blue-200 hover:border-blue-300'
  },
  {
    title: 'Teacher Management',
    description: 'Manage teacher profiles and information',
    href: '/dashboard/teachers',
    roles: ['admin', 'owner'],
    color: 'border-green-200 hover:border-green-300'
  },
  {
    title: 'Leave Management',
    description: 'Handle leave requests and approvals',
    href: '/dashboard/leaves',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-yellow-200 hover:border-yellow-300'
  },
  {
    title: 'Notice Management',
    description: 'Create and manage college notices',
    href: '/dashboard/notices',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-purple-200 hover:border-purple-300'
  },
  {
    title: 'Exam Results',
    description: 'Upload and manage exam results',
    href: '/dashboard/results',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-red-200 hover:border-red-300'
  },
  {
    title: 'Notes Management',
    description: 'Upload and organize study materials',
    href: '/dashboard/notes',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-indigo-200 hover:border-indigo-300'
  },
  {
    title: 'Bus Service',
    description: 'Manage bus routes and schedules',
    href: '/dashboard/bus',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-orange-200 hover:border-orange-300'
  },
  {
    title: 'Syllabus Management',
    description: 'Upload and update course syllabus',
    href: '/dashboard/syllabus',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-teal-200 hover:border-teal-300'
  },
  {
    title: 'Time Table',
    description: 'Create and manage class schedules',
    href: '/dashboard/timetable',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-pink-200 hover:border-pink-300'
  },
  {
    title: 'Attendance Management',
    description: 'Track and manage student attendance',
    href: '/dashboard/attendance',
    roles: ['admin', 'teacher', 'owner'],
    color: 'border-cyan-200 hover:border-cyan-300'
  },
  {
    title: 'AI Search Assistant',
    description: 'Search college information with AI help',
    href: '/dashboard/search',
    roles: ['student'],
    color: 'border-violet-200 hover:border-violet-300'
  },
  {
    title: 'Password Reset',
    description: 'Change your account password',
    href: '/dashboard/password',
    roles: ['admin', 'teacher', 'student', 'owner'],
    color: 'border-gray-200 hover:border-gray-300'
  }
];

export default function DashboardPage() {
  const { user } = useAuth();

  if (!user) return null;

  const filteredCards = dashboardCards.filter(card => 
    card.roles.includes(user.role)
  );

  const getWelcomeMessage = () => {
    switch (user.role) {
      case 'admin':
        return 'Welcome to the Admin Dashboard. You have full access to all system features.';
      case 'teacher':
        return 'Welcome to the Teacher Dashboard. Manage your classes and student records.';
      case 'student':
        return 'Welcome to the Student Portal. Use our AI assistant to search for information.';
      case 'owner':
        return 'Welcome to the Owner Dashboard. You have complete system control.';
      default:
        return 'Welcome to the College Management System.';
    }
  };

  return (
    <div className="space-y-6">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          Dashboard
        </h1>
        <p className="text-gray-600">
          {getWelcomeMessage()}
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredCards.map((card) => (
          <Link key={card.title} href={card.href}>
            <Card className={`h-full transition-all duration-200 hover:shadow-md ${card.color} cursor-pointer`}>
              <CardHeader>
                <CardTitle className="text-lg">{card.title}</CardTitle>
                <CardDescription>{card.description}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="text-sm text-gray-500">
                  Click to access this module
                </div>
              </CardContent>
            </Card>
          </Link>
        ))}
      </div>

      {user.role === 'student' && (
        <div className="mt-8 p-6 bg-blue-50 rounded-lg border border-blue-200">
          <h2 className="text-xl font-semibold text-blue-900 mb-2">
            Student Information
          </h2>
          <p className="text-blue-700">
            As a student, you can use our AI-powered search to find information about courses, 
            schedules, notices, and other college-related queries. Click on the AI Search Assistant 
            above to get started.
          </p>
        </div>
      )}

      {(user.role === 'admin' || user.role === 'owner') && (
        <div className="mt-8 p-6 bg-green-50 rounded-lg border border-green-200">
          <h2 className="text-xl font-semibold text-green-900 mb-2">
            System Overview
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-4">
            <div className="text-center">
              <div className="text-2xl font-bold text-green-700">150+</div>
              <div className="text-sm text-green-600">Total Students</div>
            </div>
            <div className="text-center">
              <div className="text-2xl font-bold text-green-700">25+</div>
              <div className="text-sm text-green-600">Faculty Members</div>
            </div>
            <div className="text-center">
              <div className="text-2xl font-bold text-green-700">12</div>
              <div className="text-sm text-green-600">Active Courses</div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
